.breadcrumb {
  background: #e5e5e5;
  padding: 15px 0;
  position: relative;
  @include r($mobile) {
    padding: 8px 0; }
  a {
    color: #8e8e8e;
    font-size: 12px;
    text-transform: uppercase;
    text-decoration: none;
    position: relative;
    margin-right: 20px;
    @include r($tablet) {
      margin-right: 10px; }
    &:last-child {
      color: $primary;
      font-weight: 600;
      &:before {
        content: none; } }
    &:hover {
      text-decoration: underline; }
    &:before {
      content: '/';
      position: absolute;
      right: -13px;
      top: 49%;
      transform: translateY(-50%);
      font-size: 12px;
      color: #8e8e8e;
      @include r($tablet) {
        right: -8px; } } }
  .close {
    position: relative;
    a {
      position: absolute;
      right: 0;
      bottom: 0px;
      margin-right: 0;
      font-size: 20px;
      font-weight: 300;
      letter-spacing: 1px;
      color: $black-three;
      text-transform: none;
      transition: all 0.3s;
      @include r($mobile) {
        bottom: -40px;
        font-size: 17px; }
      &:hover {
        text-decoration: none;
        color: $primary; }
      i {
        position: absolute;
        left: -35px;
        font-size: 17px;
        top: 5px; } } } }
